.history {
  width: 100%;
  max-width: 1170px;
  margin: auto;
  padding-top: 80px;
  background-color: #f3f3f3;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 1px;

  .dont-have {
    font-size: 25px;
    font-weight: 600;
    padding: 0 20px 15px 20px;
    max-width: 1170px;
    width: 100%;
    margin: auto;
  }

  button {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px 20px;
    background-color: #ffffff;
    width: fit-content;
    margin: 0 20px 20px 0;
  }

  .btn {
    align-self: flex-start;
    padding: 10px 20px;
    margin-top: 30px;
    button {
      color: #fff;
      background-color: #111;
      border-radius: 5px;
    }
  }
}
